<template> 
  <div> 
    <h1>番剧播放器</h1> 
    <router-link to="../index.html">返回首页</router-link> 
     <h2>{{ title }}</h2>
    <video v-if="currentEpisode" :src="currentEpisode" controls></video> 
  </div> 
</template> 
<script>
import animes from '../assets/source.js'; 
export default { 
data() { 
  return { 
    animes, 
    currentEpisode: null, 
    filterId: null,
    title: null
  }; 
}, 
created() { 
  this.loadEpisode(); 
}, 
methods: { 
  loadEpisode() { 
    const animeId = this.$route.params.animeId; 
    const episodeId = this.$route.params.episodeId; 
    const anime = this.animes.find(anime => anime.id === animeId); 
    if (anime) { 
      //this.currentEpisode = anime.links.find(link => Object.keys(link)[0] === episodeId); 
      //.episodeId
      const data = anime.links.find(link => Object.keys(link)[0] === episodeId);
      this.title = anime.title;
      this.currentEpisode = data[episodeId];
      console.log(this.currentEpisode)
    } 
  }, 
  filterAnimes() { 
    const id = this.$route.query.id; 
    if (id) { 
      this.filteredAnimes = this.animes.filter(anime => anime.id === id); 
    } else { 
      this.filteredAnimes = this.animes; 
    } 
  } 
}, 
watch: { 
  '$route.params.animeId'() { 
    this.loadEpisode(); 
  }, 
  '$route.params.episodeId'() { 
    this.loadEpisode(); 
  }, 
  '$route.query.id'() { 
    this.filterAnimes(); 
  } 
}, 
computed: { 
  filteredAnimes() { 
    return this.animes; 
  } 
} 
}; 
</script> 
