import Vue from 'vue';
import VueRouter from 'vue-router';
import AnimePlayer from '../components/AnimePlayer';
Vue.use(VueRouter);
 const routes = [
  {
    path: '/id/:animeId/num/:episodeId',
    component: AnimePlayer
  }
];
 const router = new VueRouter({
  mode: 'history',
  routes
});
 export default router;